import {Button, Modal} from "antd";
import React, {FC} from "react";

interface HelpModalProps {
    readonly maxGuesses: number;
    readonly isOpen: boolean;
    onClose(): void;
}
const HelpModal: FC<HelpModalProps> = ({maxGuesses, isOpen,onClose}) => {
    return(
    <Modal
        open={isOpen}
        onCancel={onClose}
        footer={[
            <Button key="close" onClick={onClose}>
                Close
            </Button>
        ]}
    >
        <div
            style={{
                fontFamily: "TT Norms Pro, sans-serif",
                flexDirection: "column",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center"
            }}
        >
            <h1>You have <span style={{color: "green"}}>{maxGuesses}</span> attempts to guess randomly
                generated <span style={{color: "#004254"}}>Mystery colleague</span></h1>
            <p>Each color of table cell's text will guide you to correct answer...</p>
            <p>
                <span style={{fontWeight: "bold", color: "green"}}>Green</span>{" "}
                color cell indicates a correct guess.
            </p>
            <p>
                <span style={{fontWeight: "bold", color: "orange"}}>Orange</span>{" "}
                color cell indicates a close guess.
            </p>
            <p>Black color cell indicates an incorrect guess.</p>
            <p>Hover on each answer for more help.</p>
            <p>Star (*) next to age means that age is estimated :).</p>
        </div>
    </Modal>)

}

export default HelpModal;

