import {Button, Modal, Table} from "antd";
import React, {FC, useEffect, useState} from "react";

interface StatsModalProps {
    readonly stats: any;
    readonly isOpen: boolean;
    onClose(): void;
}


const StatsModal: FC<StatsModalProps> = ({stats, isOpen,onClose}) => {
    const [winPercentage, setWinPercentage] = useState("0");
    const [streak, setStreak] = useState(0);


    useEffect(() => {
        const calculateWinPercentage = () => {
            const numWins = stats.filter((stat) => stat.guessedCorrectly).length;
            if(stats.length !== 0){
                const percentage = (numWins / stats.length) * 100;
                setWinPercentage(percentage.toFixed(2));
            }
        };

        const calculateStreak = () => {
            let currentStreak = 0;
            let longestStreak = 0;

            for (let i = 0; i < stats.length; i++) {
                if (stats[i].guessedCorrectly) {
                    currentStreak++;
                } else {
                    longestStreak = Math.max(currentStreak, longestStreak);
                    currentStreak = 0;
                }
            }

            longestStreak = Math.max(currentStreak, longestStreak);
            setStreak(longestStreak);
        };

        calculateWinPercentage();
        calculateStreak();
    }, [stats]);

    const statsTableColumns = [
        {
            title: "Guess",
            dataIndex: "guessedCorrectly",
            render: (text) => {
                return  (text ? <span style={{fontWeight: 'bold', color:'green'}}>Correct</span> :
                    <span style={{fontWeight: 'bold', color:'red'}}>Incorrect</span>)}
        },
        { title: "Tries", dataIndex: "numTries" },
        {
            title: "Mystery Colleague",
            dataIndex: "mysteryColleague",
            render: (colleague) => colleague.Name
        },
        { title: "Timestamp", dataIndex: "timestamp" }
    ]

    return(
        <Modal
            open={isOpen}
            onCancel={onClose}
            footer={[
                <Button key="close" onClick={onClose}>
                    Close
                </Button>
            ]}
            style={{ minWidth: '600px' }}
        >
            <h1>You've played <span style={{color:'green', fontWeight: "bold"}}>{stats.length}</span> games
                and won <span style={{color: Number(winPercentage) > 50 ? 'green': 'red', fontWeight: "bold"}}>{winPercentage}%</span> of them</h1>
            <h2>Your longest streak is <span style={{color:'green', fontWeight: "bold"}}>{streak}</span> games</h2>
            <div style={{ maxHeight: '400px', overflowY: 'auto' }}>
                <Table
                    columns={statsTableColumns}
                    dataSource={stats}
                    pagination={false}
                    rowKey={(record, index) => index}
                />
            </div>
        </Modal>)

}

export default StatsModal;

