import React, {FC} from "react";
import {Tag} from "antd";

interface GuessCounterProps {
  readonly maxGuesses: number;
  readonly numTries: number;
}
const GuessCounter: FC<GuessCounterProps> = ({maxGuesses,numTries}) => {
  return(
    <div style={{ marginTop: "16px" }}>
      {[...Array(maxGuesses)].map((_, index) => (
          <Tag
              key={index}
              color={index < numTries ? "green" : undefined}
              style={{ marginRight: "5px",borderColor: index < numTries ? "green" : undefined }}
          />
      ))}
    </div>)

}

export default GuessCounter;

