import React, {FC} from "react";
import {Table} from "antd";

interface GuessTableProps {
    readonly selectedColleagues;
    readonly mysteryColleague;
}
const GuessTable: FC<GuessTableProps> = ({selectedColleagues,mysteryColleague}) => {
    const guessTableColumns = [
        {
            title: "Name",
            dataIndex: "Name",
            key: "Name",
            width: "150px",
            render: (text, record) => {
                if (record.Name === mysteryColleague.Name) {
                    return (
                        <span style={{ color: "green", fontWeight: "bold" }}>{text}</span>
                    );
                }
                return text;
            }
        },
        {
            title: "Age",
            dataIndex: "Age",
            key: "Age",
            render: (text, record) => {
                const ageDifference = record.Age - mysteryColleague.Age;
                let symbol = "";
                if (ageDifference < 0) {
                    symbol = "↑";
                } else if (ageDifference > 0) {
                    symbol = "↓";
                }
                if (record.Age === mysteryColleague.Age) {
                    return (
                        <span title={"Correct"} style={{ color: "green", fontWeight: "bold" }}>{text}</span>
                    );
                } else if (Math.abs(record.Age - mysteryColleague.Age) <= 3) {
                    let hint = "";
                    if (ageDifference < 0) {
                        hint = "Close, but Mystery colleague is older.";
                    } else if (ageDifference > 0) {
                        hint = "Close, but Mystery colleague is younger.";
                    }
                    return (
                        <span title={hint} style={{ color: "orange", fontWeight: "bold" }}>{text + " " + symbol}</span>
                    );
                }
                let hint = "";
                if (ageDifference < 0) {
                    hint = "Mystery colleague is older.";
                } else if (ageDifference > 0) {
                    hint = "Mystery colleague is younger.";
                }
                return <span title={hint}>{text + " " + symbol}</span>;
            }
        },
        {
            title: "Project",
            dataIndex: "Project",
            key: "Project",
            render: (text, record) => {
                if (record.Project === mysteryColleague.Project) {
                    return (
                        <span title={"Correct"} style={{ color: "green", fontWeight: "bold" }}>{text}</span>
                    );
                }
                return (
                    <span title="Incorrect, colleague is working on different project">{text}</span>
                );
            }
        },
        {
            title: "Position / Team",
            dataIndex: "Team",
            key: "Team",
            width: "190px",
            render: (text, record) => {
                if (
                    record.Team[0] === mysteryColleague.Team[0] &&
                    record.Team[1] === mysteryColleague.Team[1]
                ) {
                    return (
                        <span title={'Correct'} style={{ color: "green", fontWeight: "bold" }}>{text[0] + " / " + text[1]}</span>
                    );
                } else if (
                    record.Team[0] === mysteryColleague.Team[0] ||
                    record.Team[1] === mysteryColleague.Team[1]
                ) {
                    return (
                        <span
                            title="Close, one of Position or Team is correct"
                            style={{ color: "orange", fontWeight: "bold" }}
                        >
                          {text[0] + " / " + text[1]}
                        </span>
                    );
                } else {
                    return(
                        <span title="Incorrect guess">{text[0] + " / " + text[1]}</span>
                    );
                }
            }
        },
        {
            title: "State",
            dataIndex: "State",
            key: "State",
            render: (text, record) => {
                if (record.State === mysteryColleague.State) {
                    return (
                        <span title={'Correct'} style={{ color: "green", fontWeight: "bold" }}>{text}</span>
                    );
                }
                return <span title={'Incorrect guess'}>{text}</span>;
            }
        }
    ];
    return(
        <Table
            dataSource={selectedColleagues}
            columns={guessTableColumns}
            pagination={false}
            style={{ marginTop: "16px" }}
            rowKey={ (record,index) => index}
        />)

}

export default GuessTable;

