import {Button, Modal} from "antd";
import React, {FC} from "react";

interface ResultModalProps {
    readonly mysteryColleague;
    readonly guessed: boolean;
    readonly failed: boolean;
    readonly numTries: number;
    readonly isOpen: boolean;
    onClose(): void;
}

const ResultModal: FC<ResultModalProps> = ({mysteryColleague, guessed, failed, numTries, isOpen, onClose}) => {
    return (<>
        {guessed && (
            <Modal
                open={isOpen}
                onCancel={onClose}
                footer={[
                    <Button key="close" onClick={onClose}>
                        Close
                    </Button>
                ]}
            >
                <div
                    style={{
                        fontFamily: "TT Norms Pro, sans-serif",
                        flexDirection: "column",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        textAlign: "center"
                    }}
                >
                    <h1 style={{justifyContent: "center", color: "green"}}>
                        WELL
                        <br/>
                        DONE!
                    </h1>
                    <p>
                        You got today's Mystery colleague with{" "}
                        <span style={{fontWeight: "bold", color: "green"}}>
                            {numTries}
                        </span>{" "}
                        tries!{" "}
                    </p>
                    {/*<p>TODO: You are on a n day streak!</p>*/}
                    <p>
                        You correctly guessed the colleague:{" "}
                        <span style={{fontWeight: "bold"}}>{mysteryColleague.Name}</span>
                    </p>
                    {/*<p>New Mystery colleague will be ready to guess in:</p>
                    <Countdown/>*/}
                </div>
            </Modal>
        )}
        {!guessed && failed && (
            <Modal
                open={isOpen}
                onCancel={onClose}
                footer={[
                    <Button key="close" onClick={onClose}>
                        Close
                    </Button>
                ]}
            >
                <div
                    style={{
                        fontFamily: "TT Norms Pro, sans-serif",
                        flexDirection: "column",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        textAlign: "center"
                    }}
                >
                    <h1 style={{color: 'red'}}>
                        TOO
                        <br/>
                        BAD!
                    </h1>
                    <p>
                        Sorry, you were unable to guess the colleague within the allowed
                        tries.
                    </p>
                    <p>
                        Today's Mystery colleague is:{" "}
                        <span style={{fontWeight: "bold"}}>{mysteryColleague.Name}</span>
                    </p>
                    {/*<p>New Mystery colleague will be ready to guess in:</p>
                    <Countdown/>*/}
                </div>
            </Modal>
        )}
    </>)

}

export default ResultModal;

