import React, {FC} from "react";
import {Table, Tooltip, Tour, TourProps} from "antd";

interface TourComponentProps {
    readonly selectedColleagues;
    readonly ref1;
    readonly ref2;
    readonly ref3;
    readonly ref4;
    readonly isOpen: boolean;
    onClose(): void;
}
const OnboardingTour: FC<TourComponentProps> = ({selectedColleagues,ref1,ref2,ref3,ref4,isOpen,onClose}) => {

    const guessTableColumns = [
        {
            title: "Name",
            dataIndex: "Name",
            key: "Name",
            width: "150px",
            render: (text) => text
        },
        {
            width: 65,
            title: "Age",
            dataIndex: "Age",
            key: "Age",
            render: (text) => {
                let symbol = "↑";
                let hint = "Close, but Mystery colleague is older.";
                return (
                    <Tooltip color={'rgba(0, 0, 0, 0.55)'} open={true} placement={'left'} title="Mystery colleague is older and age difference is only few years">
                        <span title={hint} style={{ color: "orange", fontWeight: "bold" }}>{text + " " + symbol}</span>
                    </Tooltip>
                )
            }
        },
        {
            title: "Project",
            dataIndex: "Project",
            key: "Project",
            render: (text) => (
                <Tooltip color={'rgba(0, 0, 0, 0.55)'} open={true} placement={'topLeft'} title="Correct! Mystery colleague works on same project">
                    <span title={"Correct!"} style={{ color: "green", fontWeight: "bold" }}>{text}</span>
                </Tooltip>
            )
        },
        {
            title: "Position / Team",
            dataIndex: "Team",
            key: "Team",
            width: "190px",
            render: (text) => (
                <Tooltip color={'rgba(0, 0, 0, 0.55)'} open={true} placement={"bottom"} title="Close! One of Position/Team is correct, make different guess to narrow it down">
          <span title="Close, one of Position or Team is correct" style={{color: "orange", fontWeight: "bold"}}>
            {text[0] + " / " + text[1]}
          </span>
                </Tooltip>
            )
        },
        {
            title: "State",
            dataIndex: "State",
            key: "State",
            render: (text) => (
                <Tooltip color={'rgba(0, 0, 0, 0.55)'} open={true} placement={'right'} title="Incorrect! Mystery colleague is from different state">
                    <span title={'Incorrect guess'}>{text}</span>
                </Tooltip>
            )
        }
    ];

    const steps: TourProps['steps'] = [
        {
            title: 'Start',
            description: 'Make your first guess by clicking into this input field and by selecting any desired colleague. (Next button will appear after first guess)',
            target: () => selectedColleagues.length === 0 ? ref1.current : undefined,
            nextButtonProps: {
                style:{ visibility: selectedColleagues.length === 0 ? 'hidden': 'visible'}
            }
        },
        {
            title: 'Understanding colored hints',
            description:
                <>
                    <p>
                        <span style={{fontWeight: "bold", color: "green"}}>Green</span>{" "} color cell indicates a correct guess
                    </p>
                    <p>
                        <span style={{fontWeight: "bold", color: "orange"}}>Orange</span>{" "} color cell indicates a close guess.
                    </p>
                    <p>Black color cell indicates an incorrect guess.</p>
                    <Table
                        dataSource={[{id: undefined, Name: 'Jan Novak',Age: "29",Project: "WS",Team: ["Retrieval","DEV"],State: "CZ"}]}
                        pagination={false}
                        columns={guessTableColumns}>
                    </Table>
                </>,
            placement: "top",
            target: () => ref2.current,
            style: {minWidth:"500px", width: "50%"}
        },
        {
            title: 'How To Play',
            description: 'Before start, please read carefully "How To Play" section.',
            target: () => ref3.current,
        },
        {
            title: 'Stats',
            description: 'You can see your play statistics here',
            target: () => ref4.current,
        },
        {
            title: 'Last words',
            description: 'Enjoy the game, and please share any feedback with MBA',
        },
    ];

    return(<Tour open={isOpen} onClose={onClose} steps={steps} />
    )
}

export default OnboardingTour;

